export default {
	data() {
		return {
			
		}
	},
	methods: {
		// 搜索热门
		myajax:function(){
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    header: {
			        'custom-header': 'hello' //自定义请求头信息
			    },
			    success: (res) => {
			        console.log(res.data);
			        this.text = 'request success';
			    }
			});
		},
		// 返回上一页
		back:function(){
			uni.navigateBack();
		}
	},
	onShow:function(){
		this.myajax()
	}
}
